








import { Component, Vue, Prop } from "vue-property-decorator";

@Component
export default class NoData extends Vue {
  @Prop({
    type: String,
    default: "暂无数据"
  })
  text: string | undefined;

  @Prop({
    type: String,
    default: require("./no-data.svg")
  })
  imgUrl: string | undefined;
}
